* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body,
input,
textarea,
button {
  font-size: 16px;
  color: #4e4e4e;
}

body {
  overflow-x: hidden;

}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #292929;
  margin: 0;
}

h1 {
  font-size: 34px;
  color: #262b6a;
  font-weight: 400;
}

h2 {
  font-size: 28px;
  color: #292929;
  font-weight: 400;
}

h3 {
  font-size: 27px;
  color: #262b6a;
  font-weight: 700;
}

h4 {
  font-size: 22px;
  color: #292929;
}

h5 {
  font-size: 15px;
  color: #262b6a;
  font-weight: 700;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  color: #262b6a;
  outline: none;
  transition: 0.3s all ease;
  display: inline-block;
  text-decoration: none;
}

a:hover,
a:focus {
  text-decoration: none;
}

p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

button {
  background: transparent;
  border: none;
}

button:focus {
  border: none;
  outline: none;
}

.for-mobile {
  display: block;
}

.for-desktop {
  display: none;
}

.content-wrapper {
  overflow: hidden;
}

/* Container */
.container {
  max-width: 1397px;
  width: 100%;
  margin: 0 auto;
}

.container-fluid {
  max-width: 1920px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

/* Container End */

.no-pad {
  padding: 0;
}

.no-padding {
  margin: 0;
}

.no-padding>[class*=col-] {
  padding: 0;
}

.text-align-right {
  text-align: right;
}

.body-wrapper {
  width: 100%;
  overflow-x: hidden;
}