$mq-laptop					: 1800px !default;
$mq-tablet-ls				: 1199px !default;
$mq-tablet-pr				: 992px !default;
$mq-mobile-ls				: 768px !default;
$mq-mobile-pr				: 576px !default;
$mq-mobile				    : 320px !default;

// @mixin laptop {
// 	@media (max-width : $mq-laptop) {
// 		@content;
// 	}
// }

//
@mixin xl-s {
  @media (max-width : $mq-tablet-ls) {
    @content;
  }
}

@mixin lg-s {
  @media (max-width : $mq-tablet-pr) {
    @content;
  }
}

@mixin md-s {
  @media (max-width : $mq-mobile-ls) {
    @content;
  }
}

@mixin sm-s {
  @media (max-width : $mq-mobile-pr) {
    @content;
  }
}
//
@mixin xs-s {
  @media (max-width : $mq-mobile) {
    @content;
  }
}