/* New Header Starts */
.search-dropdown .container {
  padding: 0 15px;
}

.header-top {
  background: #fce4ec;
}

.header-top p {
  color: rgb(97, 18, 65);
  font-family: 'ITC Avant Garde Gothic', sans-serif;
  text-align: center;
  font-weight: 700;
  font-size: 12px;
  line-height: 19px;
  padding: 10px 15px 14px;
  letter-spacing: 0.06em;
  word-spacing: 0.03em;
}

.header-bottom {
  background: #f64899;
  /* box-shadow: 0 0px 8px rgba(0, 0, 0, 0.3); */
  min-height: 45px;
}

.header-bottom .inner,
.header-bottom .nav-bar,
.header-bottom .social-icons {
  display: flex;
  align-items: center;
  justify-content: center;
}

.header-bottom .nav-bar>li {
  position: relative;
}

.header-bottom .nav-bar>li>a,
.header-bottom .social-icons>li>a {
  font-size: 15px;
  line-height: 20px;
  font-weight: 600;
  color: #fff;
  text-decoration: none;
  font-family: "ITC Avant Garde Gothic", sans-serif;
  position: relative;
}

.header-bottom .nav-bar>li>a {
  display: flex;
  align-items: center;
  padding: 23px 20px 24px;
}

.header-bottom .fa {
  margin-right: 10px;
}

.header-bottom .fa.fa-angle-down {
  margin-left: 10px;
}

.dp-down:after {
  content: "\f107";
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  display: inline-block;
  font: normal normal normal 16px/1 FontAwesome;
}

.header-bottom .social-icons {
  margin-left: 10px;
}

.header-bottom .social-icons a {
  padding: 23px 5px 24px;
}

.header-bottom .social-icons li {
  position: relative;
}

.header-bottom .social-icons li .tooltip {
  font-family: "ITC Avant Garde Gothic", sans-serif;
  position: absolute;
  left: -38px;
  top: 100%;
  background-color: #fff;
  border-radius: 8px;
  padding: 8px 15px;
  font-size: 14px;
  z-index: -1;
  opacity: 0;
  transition: 0.3s all ease;
  min-width: 75px;
  box-shadow: 0px 0px 80px rgba(0, 0, 0, 0.16);
  text-align-last: center;
}

.header-bottom .social-icons li:hover .tooltip {
  opacity: 1;
  z-index: 99;
}

.header-bottom .sub-menu {
  min-width: 960px;
  position: absolute;
  left: 0;
  top: 100%;
  z-index: -1;
  opacity: 0;
  transition: 0.3s all ease;
}

.header-bottom .sub-menu .fa {
  color: rgba(97, 18, 65, 1);
}

.header-bottom .sub-menu span {
  display: block;
  text-align: center;
  color: rgba(97, 18, 65, 1);
  font-size: 13px;
  line-height: 18px;
  font-weight: 400;
  font-family: "ITC Avant Garde Gothic", sans-serif;
  transition: 0.3s all ease;
}

.header-bottom .sub-menu .sub-desp {
  color: rgba(246, 72, 153, 1);
  font-size: 12px;
}

.header-bottom .sub-text-wrap:hover .sub-desp {
  color: rgba(97, 18, 65, 1);
}

.header-bottom .sub-menu .sub-link {
  width: 192px;
  padding: 12px 15px;
}

.header-bottom .sub-menu .sub-link img {
  height: 162px;
  object-fit: cover;
  margin-bottom: 12px;
}

.header-bottom .sub-menu {
  display: flex;
  background-color: rgba(252, 228, 236, 1);
  border: 1px solid rgb(209, 209, 209);
}

.header-bottom .nav-bar li:hover .sub-menu {
  opacity: 1;
  z-index: 999;
}

.header-bottom .nav-bar .drop-down .sub-menu {
  display: block;
  min-width: 265px;
}

.header-bottom .nav-bar .drop-down .sub-link {
  display: flex;
  align-items: center;
  width: auto;
}

.header-bottom .drop-down .sub-menu span {
  text-align: left;
}

.header-bottom .nav-bar>li.close-menu-btn,
.nav-toggle-btn {
  display: none;
}

/* New Header Ends */
/* header css ends here */



/* banner css strats here */
.banner-wrap {
  background-image: url(../images/collagen_desktop_2.jpg);
  background-repeat: no-repeat;
  background-position: left top;
  background-size: 104%;
  background-color: #fde4ec;
  position: relative;
  padding: 90px 0;
}

.banner-wrap .banner-info {
