}

.banner-wrap .buy-now {
  margin-top: 50px;
  display: flex;
}

.banner-wrap .buy-now a {
  font-family: 'ITC Avant Garde Pro Md';
  font-weight: 500;
  font-size: 31px;
  line-height: 37px;
  color: #ffffff;
  width: 50%;
  height: 75px;
  display: flex;
  align-items: center;
  justify-content: center;
