  margin-bottom: 40px;
}

.banner-wrap .free-shipping p {
  font-family: 'ITC Avant Garde Pro Md';
  font-weight: bold;
  font-size: 31px;
  line-height: 37px;
  scroll-margin-right: 5px;
}

.banner-wrap .free-shipping img {
  height: 26px;
  animation: moving 1s linear infinite;
}

@keyframes moving {
  from {
    margin-left: 0px;
  }

  to {
    margin-left: 50px;
  }
}

ul.services {
  display: flex;
}

ul.services li {
  margin-right: 40px;
}

ul.services li .service-img {
