/* Packages section css starts */
.package_section {
  background-color: rgba(252, 228, 236, 1);
  font-family: 'ITC Avant Garde Pro BK';
}




.package_section {
  padding: 50px 0px 100px 0px;
}



.package_section .top-heading {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 25px;
}

.package_section .top-heading span:first-child {
  color: #16555E;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 1.6em;
  margin-bottom: 10px;
  font-family: 'ITC Avant Garde Pro BK';
}

.package_section .top-heading h3 {
  color: #F64899;
  font-size: 39px;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 1em;
  font-family: 'ITC Avant Garde Pro BK';
}

.package_section .top-heading span {
  color: #16555E;
  font-size: 25px;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 1.6em;
  font-family: 'ITC Avant Garde Pro BK';
}

.packages_box {
  display: flex;
  justify-content: space-between;
}

.packages_box .package {
  max-width: 32%;
  width: 100%;
  display: inline-block;
  flex-direction: column;
  align-items: center;
  box-shadow: -1px 0px 20px 0px rgba(246, 72, 153, 0.1);
  transition: background 0.3s, border 0.3s, border-radius 0.3s, box-shadow 0.3s;
  padding: 35px 15px 20px 15px;
  background-color: #fff;
  text-align: center;
  transform: scale(1);
  transition: 0.3s all ease-in-out;
  border: 2px solid;
  border-color: transparent;
}

.packages_box .package h4 {
  color: #611241;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.6em;
  font-family: 'ITC Avant Garde Pro BK';
}

.packages_box .package span {
  color: #f64899;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.6em;
  letter-spacing: 0.5px;
  display: block;
  font-family: 'ITC Avant Garde Pro BK';
}

.packages_box .package img {
  width: 100%;
}

.packages_box .package>img {
  padding: 0 65px;
}

